@import "https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700;800&display=swap";
* {
  font-family: Poppins, sans-serif;
}

body {
  background-color: bisque;
}

h1 {
  width: 100%;
  color: #000;
  letter-spacing: 10px;
  text-transform: uppercase;
  justify-content: center;
  margin: auto;
  font-size: 5em;
  font-weight: 800;
  line-height: 1em;
  display: flex;
}

form {
  width: 20%;
  height: 90px;
  flex-direction: column;
  align-items: center;
  margin: 20px auto auto;
  display: flex;
}

form input {
  width: 50%;
  margin: 10px;
}

form button {
  width: 100px;
  height: 25px;
  color: #fff;
  cursor: pointer;
  background-color: #b9d9ba;
  border: none;
  border-radius: 4px;
  font-size: 14px;
  transition: background-color .3s;
}

form button:hover {
  background-color: #99ea63;
}

.rpsContainer {
  width: 52%;
  height: 220px;
  justify-content: center;
  margin: 120px auto;
  display: flex;
}

.rpsContainer img:hover {
  border: 3px solid #c2ff85;
  transition: transform .5s;
  transform: scale(1.1);
}

.rpsImg {
  width: 100%;
  height: 100%;
  border-radius: 30%;
  margin: 5px 7px;
}

.paragraphContainer {
  width: 80%;
  flex-flow: wrap;
  justify-content: space-around;
  font-size: 20px;
  display: flex;
  position: fixed;
  bottom: 20px;
  left: 10%;
}

#computerChoiceParagraph, #computerScore {
  color: red;
}

.popUpWindow {
  width: 50%;
  height: 60%;
  z-index: 999;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  animation: 1s ease-out spin-in;
  display: none;
  position: fixed;
  top: 15%;
  left: 25%;
}

#imgWin, #imgLose {
  height: 100%;
  width: 100%;
}

#playAgainButton {
  height: 30px;
  width: 100px;
  color: #fff;
  cursor: pointer;
  background-color: #b9d9ba;
  border: none;
  border-radius: 4px;
  margin-left: 40%;
  font-size: 14px;
  position: static;
}

#playAgainButton:hover {
  background-color: #99ea63;
}

#score-list {
  text-align: center;
  flex-direction: column;
  display: flex;
  position: fixed;
  top: 100px;
}

#score-list h1 {
  color: green;
  font-size: 40px;
}

@keyframes spin-in {
  from {
    opacity: 0;
    transform: rotate(360deg);
  }

  to {
    opacity: 1;
    transform: rotateY(0);
  }
}

